}
.my-carousal1_scroll_up__3uj6E .my-carousal1_left_bar__3lGk3 {
  position: absolute;
  z-index: 1;
  background-color: transparent;
  top: 19px;
  left: 5px;
  width: 18px;
  display: block;
  transform: rotate(-45deg);
}
